/* styles.css */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-image: url('../public/background.jpg'); /* 배경 이미지 경로 */
  background-size: cover; /* 이미지를 화면에 맞게 조정 */
  background-repeat: repeat-y; /* 이미지 반복 방지 */
  background-position: center center; /* 이미지 중앙 배치 */
  height: 100vh; /* 전체 높이를 채움 */
  width: 100vw; /* 전체 너비를 채움 */
  overflow: auto; /* 스크롤바 숨김 */
}

#root {
  height: 100%;
  width: 100%;
}
