.container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .qr-section {
    padding: 20px;
    margin-bottom: 20px;
    width: 300px;
    text-align: center;
  }
  
  .questions-section {
    padding: 20px;
    width: 300px;
    max-height: 400px;
    overflow-y: auto;
  }
  
  .questions-container {
    display: flex;
    flex-direction: column;
    color: white;
    background-color: rgba(56, 47, 128);
  }
  
  .question {
    margin-bottom: 30px;
  }

  
  .options {
    margin-top: 10px;
  }

  .options label {
    margin-right: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .student-info {
    margin-top: 20px;
    text-align: left;
    background-color: #f0f0f0;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
  }
  
  .student-photo {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .student-details {
    display: flex;
    flex-direction: column;
  }
  
  .koreanName, .englishName, .churchName {
    margin: 0;
  }
  
  .popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    z-index: 1000;
  }

  #headtitle{
    color: yellow;
    background-color: rgba(56, 47, 128);
  }