/* AttendanceStatusPage.css */

.attendance-admin-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    min-height: 100vh; /* 화면 전체를 채우기 위해 사용 */
  }
  
  h1 {
    cursor: pointer;
    text-align: center;
  }
  
  .filters {
    display: flex;
    flex-wrap: wrap;
    gap: 10px; /* 필드 간 간격 */
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .filters input,
  .filters select,
  .filters button {
    margin: 0 5px;
    padding: 10px;
    font-size: 16px;
  }
  
  .field-selection {
    margin-bottom: 20px;
    text-align: center;
  }
  
  table {
    border-collapse: collapse;
    width: 100%;
    max-width: 800px; /* 테이블 너비 제한 */
  }
  
  table th,
  table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
  }
  
  table th {
    background-color: #f4f4f4;
  }
  
  table td {
    background-color: #fff;
  }
  