.details-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    height: auto;
    text-align: center;
  }

.info-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    padding: 20px;
    border-radius: 10px;
    background-color: rgba(107, 139, 209, 0.5);
    margin-bottom: 15px;
  }
  
  #top_info {
    margin-bottom: 10px;
    background-color: #bebebe;
  }
  
  .infos {
    border-radius: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
  }
  .information {
    White-space: pre-wrap;
    flex-direction: column;
    text-align: left;
    position: relative;
  }
  .uploaded-image {
    width: 150px;
    height: 150px;
    border-radius: 5%;
    object-fit: cover;
    margin-top: 20px;
    margin-bottom: 5px;
  }
  
  .qr-code {
    width: 150px;
    height: 150px;
    border-radius: 5%;
    object-fit: cover;
  }
  
  .church {
    font-size: 16px;
    margin-bottom: 2px;
    letter-spacing: 1px;
  }
  .info {
    font-size: 20px;
    
  }
  .contact {
    font-size: 16px;
    letter-spacing: 1px;
  }
  #engName {
    margin-bottom: 15px;
  } 
