.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filters {
  display: flex;
  flex-direction: row;
  justify-content: space-between; /* 필터들 사이에 공간을 동일하게 분배 */
  margin-bottom: 20px;
}

.filter-item {
  margin-right: 10px; /* 필터들 사이에 여유 공간 추가 */
}

.filter-item select {
  padding: 5px;
  font-size: 14px;
}

.day-session-select {
  display: flex;
  gap: 20px;
  height: 40px;
}

.qr-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.attendance-list {
  width: 350px;
  height: 200px;
  overflow-y: auto;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 10px;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid black;
  padding: 20px;
  z-index: 1000;
}

.flash {
  animation: flash-animation 0.5s ease-in-out;
}

@keyframes flash-animation {
  0%,
  100% {
    background-color: white;
  }
  50% {
    background-color: yellow;
  }
}

.container .popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 1em;
  border-radius: 10px;
  z-index: 1000;
}

#headtitle{
  color: yellow;
  background-color: rgba(56, 47, 128);
}

#index {
  font-size: 24px;
  font-weight: bold;
  margin-top: 0px;
}
#studentlists{
  margin-top: 0px;
  font-size: 32px;
  font-weight: bold;
}

#infos{ 
  padding-top: 0px;
}

select {
  width: 150px;
  padding-left: 10px;
  font-size: 20px;
}

button {
  font-size: 20px;
  font-weight: bold;
}